<template>
    <div class="about" :class="active ? 'active' : ''">
        <div class="content">
            <div class="n-title">
                <div class="p1"></div>
                <div class="p2"></div>
            </div>
            <div class="detail">
                <ul>
                    <li>VOCVOCVOC smart lockVOC DoorVOC Magnetic lockVOC smart SafesVOC | GS5</li>
                    <li>VOCVOC151700VOC70</li>
                    <li>VOC</li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data(){
            return {
                active: false
            }
        },
        methods: {
            scroll(){
                let obj = document.querySelector(".ishome");
                let swidth = document.body.clientWidth;
                let screenHeight = obj.offsetHeight;   //
                let scrollTop = obj.scrollTop;   //

                let contentObj = document.querySelector(".content");
                let aboutObj = document.querySelector(".about");
                let aboutObjH= aboutObj.offsetHeight;
                let aboutObjT= aboutObj.offsetTop + contentObj.offsetTop;

                if(swidth >= 678){
                    /**
                     * aboutObjT < (scrollTop + screenHeight)    
                     * (aboutObjT + aboutObjH) > scrollTop   
                     */
                    if((aboutObjT + aboutObjH) >= scrollTop && aboutObjT < (scrollTop + screenHeight)){
                        this.active = true;
                    }
                    else{
                        this.active = false;
                    }
                }
            }
        },
        mounted() {
            var that = this;
            var contentObj = document.querySelector(".ishome");
            contentObj.addEventListener('scroll', that.scroll);
        }
    }
</script>
<style>
    .about{
        box-sizing: border-box;
        width: 100%;
        background: #fff;
        color: #333;
    }
    .about .content{
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .about .detail{
        min-height: 100px;
    }
    .about .n-title, .about .n-title .p1, .about .n-title .p2{
        color: #333;
    }
    .about .detail ul{
        list-style: none;
        padding: 0px;
        margin: 0px;
    }
    .about .detail ul li{
        padding-bottom: 12px;
        font-size: 13px;
        padding: 20px;
    }
    .about .detail ul li:last-child{
        padding-bottom: 0px;
    }
    .about.active .n-title, .about.active .detail{
        animation-name: fadeIn;
    }
    @media (min-width: 300px){
        .about .detail ul li{
            padding: 8px 20px;
        }
    }
    @media (min-width: 768px){
        .about .detail, .about .n-title{
            animation: 1s ease  forwards  alternate;
        }
        .about .detail ul li {
            padding-left: 20px;
            padding-right: 20px;
        }
        .about{
            padding-top: 45px;
            padding-bottom: 45px;
        }
    }
    @media (min-width: 992px){
        .about .detail ul li{
            padding: 20px 20px;
        }
    }
    @media (min-width: 1200px){
        .about .detail ul li {
            padding-bottom: 20px;
            font-size: 14px;
            padding-left: 20px;
            padding-right: 20px;
        }
        .about{
            padding-top: 60px;
            padding-bottom: 60px;
        }
        .about .content{
            width: 1100px;
            margin: 0px auto;
        }
    }
    @media (min-width: 1500px){
        .about .content{
            width: 1300px;
            margin: 0px auto;
        }
    }
    @media (min-width: 1800px){
         .about .content{
            width: 1500px;
            margin: 0px auto;
        }
    }
</style>


